import React from 'react';
import styles from './styles.module.scss'
import {useDispatch} from "react-redux";
import {setCardID} from "../../../profileReducer";
import Rating from '@material-ui/lab/Rating';

const Rows = ({row, typeTableRequest, profileID, buttonHandler}: any) => {
    const dispatch = useDispatch()
    const getCardId = (id: string) => dispatch(setCardID(id))

    return <div className={styles.rowe} key={row._id}>
        <div className={styles.row}>

            <span className={styles.rowItem} id={'table'}>{typeTableRequest ? row.name : row.question}</span>
            <span className={styles.rowItem}
                  id={'table'}>{typeTableRequest ? row.cardsCount : row.answer}</span>
            <span className={styles.rowItem} id={'table'}>{row.updated.slice(0, 10)}</span>
            <span className={styles.rowItem}
                  id={'table'}> {typeTableRequest ? row.user_name :
                <Rating name="read-only" value={row.grade} readOnly/>}</span>

            {typeTableRequest && <div className={`${styles.rowItem} ${styles.btnBox}`}>
                {profileID === row.user_id &&
                (<>
                        <span className={styles.btn} data-color
                              onClick={buttonHandler.bind(null, row._id, 'delete')}>Delete</span>
                    <span className={styles.btn}
                          onClick={buttonHandler.bind(null, row._id, 'edit')}>Edit</span>
                </>)
                }
                <span onClick={getCardId.bind(null, row._id)}
                      className={styles.btn}>Learn</span>
            </div>}

        </div>
    </div>

};

export default Rows;
