import React, {ChangeEvent, useState} from 'react';
import styles from './styles.module.scss'
import {TextField} from "@material-ui/core";
import {useDispatch, useSelector} from "react-redux";
import {addCardPack, changePackName, changeTypeTable, setCardID} from "../../profileReducer";
import Modal from "../../../common/modal";
import {ReactComponent as AddedCard} from "../../../../../assets/added.svg";
import {AppRootStateType} from "../../../../m2-bll/store";

const FindTable = () => {
        const [openModal, setOpenModal] = useState(false)
        const dispatch = useDispatch()
        const onChangeHandler = (e: ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
            dispatch(changePackName(e.currentTarget.value))
        }

        const addNewPack = (value?: string) => {
            dispatch(addCardPack({cardsPack: {name: value}}))
        }

        const backToCards = () => {
            dispatch(changeTypeTable('cards'))
            dispatch(setCardID(''))
        }

        const typeGetRequest = useSelector<AppRootStateType, string>((state) => state.profile.tableType)
        const profileID = useSelector<AppRootStateType, string>((state) => state.login.profileData._id)
        const userIDCard = useSelector<AppRootStateType, string | undefined>((state) => state.profile.cardItems?.packUserId)
        const typeTableRequest = typeGetRequest === 'cards'

        return (
            <>
                {typeTableRequest ? <h1>Packs list</h1> : <h1 onClick={backToCards}>Back</h1>}
                <Modal openModal={openModal} setOpenModal={setOpenModal} setActionTC={addNewPack} type={'added'}/>
                <div className={styles.findTable}>
                    <div className={styles.findInput}>
                        <TextField id="outlined-basic"
                                   onChange={onChangeHandler}
                                   style={{width: '800px'}}
                                   variant="standard"
                                   label={'Find by name'}
                        />
                    </div>
                    {profileID === userIDCard &&
                    <AddedCard onClick={setOpenModal.bind(null, true)} className={styles.btn}/>}
                </div>
            </>
        )
    }
;

export default FindTable;
